import {Container, Bar, Site, Social} from './footerStyled';
import discord from '../../img/discord.svg';
import telegram from '../../img/telegram.svg';
import twitter from '../../img/twitter.svg';
import youtube from '../../img/youtube.svg';

const Footer = () => {
    return ( 
        <Container>
            <Bar />
            <div>
                <Site>
                    <h3>SITE MAP:</h3>
                    <ul>
                        <li>Inicio</li>
                        <li>Whitepaper</li>
                        <li>FAQ</li>
                    </ul>
                </Site>
                <Social>
                    <h3>¡Sigue nuestras redes sociales!</h3>
                    <ul>
                        <li><img src={discord}/></li>
                        <li><img src={telegram}/></li>
                        <li><img src={twitter}/></li>
                        <li><img src={youtube}/></li>
                    </ul>
                </Social>
            </div>
            <p>&copy;Todos los dereschos reservados - Crypto Cans 2022</p>
        </Container>
    );
}
 
export default Footer;