import {ContainerAbout, CardAbout} from './aboutStyled';

const About = () => {
    return ( 
        <ContainerAbout>
            <CardAbout>
                <h2>SOBRE EL PROYECTO</h2>
                <p>CryptoCans es un juego nft donde los jugadores compiten en una pista contra otros jugadores o contra la maquina buscando obtener el primero, segundo o tercer lugar para así conseguir las recompensas en el token (silver) interno para luego poder aumentar tu manada o directamente cambiarlos por tokens CCT (Crypto Cans Token) que podrás convertir en dinero real.</p>
            </CardAbout>
        </ContainerAbout> 
    );
}
 
export default About;