import { Fragment } from 'react';
import {NftContainer, Title, Content, Text, Bar, Cards, Card, CardMain, CardBody, Price, Mint, CardContainer, ContainerCard, MainContainer, BarCommon, BarRare, BarEpic, BarLegendary, CardHead, ButtonCommon, ButtonRare, ButtonEpic, ButtonLegendary} from './StyledNft';
import nftIco from '../../img/nft-ico.svg';
import commun from '../../img/can-common.svg'
import rare from '../../img/can-rare.svg'
import epic from '../../img/can-epic.svg'
import legendary from '../../img/can-legendary.svg'
import bnb from '../../img/bnb-ico.svg'

const Nft = () => {
    return ( 
        <Fragment>
            <NftContainer>
                <CardContainer>
                    <img src={nftIco} />
                    <Text>
                        <Title>         
                            <h2>TOKENS NFT’S</h2>
                        </Title>
                        <Content>Crypto Cans es un juego NFT construido en Binance Smart Chain. Tendras que entrenar a tu ciborg con el objetivo de competir contra otros dueños de ciborgs en todo el mundo. Las carreras ocurren 24/7. </Content>
                    </Text>
                    <Bar></Bar>
                </CardContainer>
                <Cards>
                    <ContainerCard>
                        <Card className='col-1'>
                            <CardHead>
                                <img src={commun} />
                                <h2>COMMON</h2>
                                <BarCommon></BarCommon>
                            </CardHead>
                            <CardBody>
                                <Mint>
                                    <p>Minted 0/100</p>
                                    <ButtonCommon href='/shop'>Mint</ButtonCommon>
                                </Mint>
                                <Price>
                                    <p>0.001</p>
                                    <div>
                                        <img src={bnb}/>
                                        <p>BNB</p>
                                    </div>
                                </Price>
                            </CardBody>
                        </Card>

                        <Card className='col-2'>
                            <CardHead>
                                <img src={rare} />
                                <h2>RARE</h2>
                                <BarRare></BarRare>
                            </CardHead>
                            <CardBody>
                                <Mint>
                                    <p>Minted 0/100</p>
                                    <ButtonRare href='/shop'>Mint</ButtonRare>
                                </Mint>
                                <Price>
                                    <p>0.002</p>
                                    <div>
                                        <img src={bnb}/>
                                        <p>BNB</p>
                                    </div>
                                </Price>
                            </CardBody>
                        </Card>

                        <Card className='col-3'>
                            <CardHead>
                                <img src={epic} />
                                <h2>EPIC</h2>
                                <BarEpic></BarEpic>
                            </CardHead>
                            <CardBody>
                                <Mint>
                                    <p>Minted 0/100</p>
                                    <ButtonEpic href='/shop'>Mint</ButtonEpic>
                                </Mint>
                                <Price>
                                    <p>0.003</p>
                                    <div>
                                        <img src={bnb}/>
                                        <p>BNB</p>
                                    </div>
                                </Price>
                            </CardBody>
                        </Card>

                        <Card className='col-4 card-legendary'>
                            <CardHead>
                                <img src={legendary} />
                                <h2>legendary</h2>
                                <BarLegendary></BarLegendary>
                            </CardHead>
                            <CardBody>
                                <Mint>
                                    <p>Minted 0/100</p>
                                    <ButtonLegendary href='/shop'>Mint</ButtonLegendary>
                                </Mint>
                                <Price>
                                    <p>0.004</p>
                                    <div>
                                        <img src={bnb}/>
                                        <p>BNB</p>
                                    </div>
                                </Price>
                            </CardBody>
                        </Card>
                    </ContainerCard>
                    <MainContainer>
                        <CardMain className='col-4'>
                            <CardHead>
                                <img src={legendary} />
                                <h2 className='h2-legendary'>LEGENDARY</h2>
                                <BarLegendary></BarLegendary>
                            </CardHead>
                            <CardBody>
                                <Mint>
                                    <p>Minted 0/100</p>
                                    <ButtonLegendary href='/shop'>Mint</ButtonLegendary>
                                </Mint>
                                <Price>
                                    <p>0.004</p>
                                    <div>
                                        <img src={bnb}/>
                                        <p>BNB</p>
                                    </div>
                                </Price>
                            </CardBody>
                        </CardMain>
                    </MainContainer>
                </Cards>
            </NftContainer>
        </Fragment>
     );
}
 
export default Nft;