import styled from 'styled-components';

export const Header = styled.div`
    margin-top: 150px;
    text-align: center;
`;

export const Title = styled.div`
    
    font-family: 'Orbitron';
    font-weight: 900;
    font-size: 7vw;
    line-height: 133px;
    letter-spacing: 0.03em;
    text-transform: uppercase;
    background: linear-gradient(89.86deg, #FF2121 0.11%, #89121C 99.86%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;

`;

export const Sub = styled.p`
    font-family: 'Orbitron';
    font-weight: 900;
    font-size: 30px;
    line-height: 133px;
    letter-spacing: 0.03em;
    text-transform: uppercase;
    color: rgba(255, 255, 255, 0.9);
    margin-bottom: 100px;

    @media (max-width: 900px) {
            margin-bottom: 10px;
        }

`;

export const Item = styled.div`
    display: flex;
    gap: 10px 100px;
    margin: 0 auto;
    position: relative;
    left: -200px;
    width: 800px;
    flex-wrap: wrap;

        @media (max-width: 600px) {
            display: grid;
            grid-template-columns: 1% 99%;
            grid-template-areas: "q year"
                                 "arrow ui";
        }


        @media (max-width: 900px) {
            position: none;
            left: 0px;
            gap: 10px 91px;
            width: 791px;
        }

    p{
        font-family: 'Orbitron';
        font-style: normal;
        font-weight: 900;
        font-size: 70px;
        line-height: 133px;
        letter-spacing: 0.03em;
        text-transform: uppercase;
        color: rgba(255, 255, 255, 0.8);

        @media (max-width: 900px) {
            font-size: 6vw;
        }

    }
    .q{
        width: 100px;
    }
    .year{
        width: 420px;
    }
    .active{
        background: linear-gradient(90deg, #FF2121 2.42%, #89121C 100%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
        text-fill-color: transparent;
        text-shadow: 0px 6px 17px rgba(255, 33, 33, 0.4);
    }
`;

export const Arrow = styled.div`
     width: 100px;
     display: flex;
     justify-content: center;
`;

export const Ul = styled.div`
    display: flex;
    flex-direction: column;
    margin-top: 20px;
    width: 600px;
    list-style-type: disclosure-closed;

    li{
        font-family: 'Gemunu Libre';
        font-weight: 500;
        font-size: 30px;
        line-height: 45px;
        letter-spacing: 0.04em;
        color: #FFFFFF;

        @media (max-width: 576px) {
            font-size: 5vw, !important;
        }

        @media (max-width: 600px) {
            width: 100%;
            max-width: 300px;
            align-items: center;
            text-align: left;
        }

        @media (max-width: 900px) {
            font-size: 3vw;
        }

    }
`;

export const Body = styled.div`
    position: relative;
    margin-left: 70px;
       
        @media (max-width: 900px) {
            display: flex;
            flex-direction: column;
            justify-content: center;
            margin-left: 10px !important;

        }

        @media (max-width: 1200px) {
            margin-left: 250px;
        }

`;
