import styled from 'styled-components';

export const TokenomicsContainer = styled.div`
    margin-top: 100px;
    padding: 0 70px;

    @media (max-width: 576px){
        margin-top: 0px;
    }
`;

export const Title = styled.h2`
    font-family: 'Orbitron';
    font-weight: 900;
    font-size: 7vw;
    line-height: 133px;
    letter-spacing: 0.03em;
    text-transform: uppercase;
    background: linear-gradient(89.86deg, #1EFC0A 0.11%, #127209 99.86%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
    text-align: end;
`;

export const Container = styled.div`
    display: flex;
    /* gap: 50px; */
    /* flex-wrap: wrap; */
    

    @media (max-width: 992px) {

        flex-direction: column;

        div{
            display: flex;
            /* width: 40%; */
            justify-content: center;
            img{
                width: 50%;
            }
        }
    }

    @media (max-width: 1200px) {
            div{      
                img{
                    width: 90%;
                }
            }
        }
    
    div{
        display: flex;
        /* width: 40%; */
        img{
            align-self: center;
            /* width: 100%; */
        }
    }

`;

export const Bar = styled.div`
    width: 8px;
    height: 51px;
    background: #1DF60A;
    justify-self: center;
    align-self: center;
`;

export const Detail = styled.div`
    display: flex;
    flex-direction: column;
    color: #FFFFFF;

    div{
        display: flex;
        gap: 20px;
    }
    
    p{

        font-family: 'Orbitron';
        font-weight: 900;
        font-size: 70px;
        line-height: 133px;
        letter-spacing: 0.03em;
        text-transform: uppercase;

        @media (max-width: 360px) {
            width: 100%;
        }

        @media (max-width: 992px) {
            font-size: 30px;
        }

        @media (max-width: 1200px) {
            font-size: 45px;
        }
    }

    .text{
        font-family: 'Gemunu Libre';
        font-weight: 500;
        font-size: 30px;
        line-height: 45px;
        letter-spacing: 0.04em;
        text-align: center;

        @media (max-width: 1200px) {
            font-size: 25px;
        }

    }
`;

export const Details = styled.div`
    width: 70%;
    display: flex;  
    flex-wrap: wrap;
    gap: 50px;

    @media (max-width: 576px) {
        justify-content: flex-start !important;
    }

    @media (max-width: 992px) {
        width: 100%;
    }

    @media (max-width: 1200px) {
        width: 100%;
        gap: 10px;
    }

`;

export const DetailData = styled.div`
    width: 220px;

    @media (max-width: 992px) {
        width: 100%;
        display: flex;
    }

    @media (max-width: 1200px) {
        width: 150px;
        display: flex;
    }

`;