import {Container, Header, CardContainer, Bar, Card } from './TeamStyled';
import ceo from '../../img/ceo.svg';
import back from '../../img/back.svg';
import front from '../../img/front.svg';
import design from '../../img/design.svg';

const Team = () => {
    return ( 
        <Container>
            <Header>
                <h2>Conoce<br/> nuestro equipo</h2>
                <Bar />
            </Header>
            <CardContainer>
                <Card className='main'>
                    <img src={ceo} />
                    <h3>Mauricio Mendoza</h3>
                    <p>Co-fundador</p>
                </Card>
                <Card>
                    <img src={back} />
                    <h3>Jason Hernadez</h3>
                    <p>Desarrollador Front-end</p>
                </Card>
                <Card>
                    <img src={front} />
                    <h3>Manuel Perez</h3>
                    <p>Desarrollador Blockchain</p>
                </Card>
                <Card>
                    <img src={design} />
                    <h3>Enny Hidalgo</h3>
                    <p>Diseñador</p>
                </Card>
            </CardContainer>
        </Container>
     );
}
 
export default Team;