import styled from 'styled-components';

export const NavStyled = styled.nav`
    height: 150px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    font-family: 'Inter', sans-serif;
    flex-wrap: wrap;

    @media (max-width: 1130px) {
            justify-content: center;
            margin-top: 30px;
        }

    @media (max-width: 1200px) {
            justify-content: center;
            gap: 0 70px;
            margin-top: 30px;
        }

`;

export const NavLink = styled.div`
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 70px;
    color: #fff;
    padding: 0 50px;

        @media (max-width: 628px) {
            gap: 0 70px;
            justify-content: center;
        }

`;

export const Link = styled.div`
    display: flex;
    list-style: none;
    gap: 60px;

        a{
            color: #fff;
            text-decoration: none;
        }

        @media (max-width: 360px) {
            flex-wrap: wrap;
            gap: 0 10px;
            justify-content: center;
        }
`;

export const NavSocial = styled.div`
    display: flex;
    align-items: center;

    @media (max-width: 360px) {
            flex-wrap: wrap;
            justify-content: center;
        }
    
`;

export const EllipseSocial = styled.span`
    top: -153px;
    position: absolute;
    background: rgba(255, 255, 255, 0.3);
    filter: blur(266px);;
    width: 340px;
    height: 331px;
`;

export const LinkSocial = styled.div`
    display: flex;
    list-style: none;
    gap: 30px;
    align-items: center;
    z-index: 1;
`;

export const Bar = styled.div`
    border: 1px solid rgba(255, 255, 255, 0.5);
    background-color: #FFFFFF;
    width: 0px;
    height: 26px;
`;

export const Wallet = styled.div`
    display: flex;
    align-items: center;
    padding: 0 30px;
    gap: 10px;
    z-index: 1;

    a{
        text-decoration: none;
        color: #fff;
    }

    @media (max-width: 463px) {
            content: "okok";
        }

`;

