import styled from 'styled-components';

export const Container = styled.footer`
    padding: 0px 70px;
    
    div{
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
    }

    p{
        font-family: 'Gemunu Libre';
        font-style: normal;
        font-weight: 700;
        font-size: 16px;
        line-height: 25px;
        letter-spacing: 1px;
        color: rgba(255, 255, 255, 0.9);
        margin-bottom: 20px;
        margin-top: 50px;
    }

    h3{
        font-family: 'Gemunu Libre';
        font-style: normal;
        font-weight: 700;
        font-size: 18px;
        line-height: 25px;
        letter-spacing: 1px;
        color: rgba(255, 255, 255, 0.9);
    }
`;

export const Bar = styled.footer`
    border: 1px solid rgba(255, 255, 255, 0.5);
    transform: rotate(-0.07deg);
    margin-bottom: 40px;
`;

export const Site = styled.div`
    display: flex;
    flex-direction: column;
    h3{
        font-family: 'Gemunu Libre';
        font-weight: 700;
        font-size: 18px;
        line-height: 25px;
        letter-spacing: 1px;
        color: rgba(255, 255, 255, 0.9);
    }

    li{
        font-family: 'Gemunu Libre';
        font-style: normal;
        font-weight: 600;
        font-size: 18px;
        line-height: 25px;
        letter-spacing: 1px;
        list-style: outside;
        color: rgba(255, 255, 255, 0.9);
        margin-left: 20px;
    }
   
`;

export const Social = styled.div`
    display: flex;
    flex-direction: column;
    align-self: flex-start;
    gap: 20px;
    ul{
        display: flex;
        justify-content: space-evenly;
        
    }
`;