import styled from "styled-components";

export const HeroContainer = styled.div`
    display: flex;
    box-sizing: border-box;
    overflow: hidden;
    height: 80vh;
    padding: 0 50px;

    /* @media (max-width: 576px) {
            height: auto !important;
    } */

    @media (max-width: 768px) {
            height: 500px;
            align-items: center;
        }

    @media (max-width: 992px) {
            height: 600px;
            align-items: center;
        }

    @media (max-width: 1400px) {
        height: 600px;
    }
    
        
`;

export const Content = styled.div`
    position: relative;
    width: 55%;
    display: flex;
    flex-direction: column;
    justify-content: center;

    @media (max-width: 360px) {
        width: 100%;
    }

    @media (max-width: 768px) {
            flex-direction: row;
            width: 100%;
        }

    h1{
        width: 100%;
        font-family: 'Orbitron', sans-serif;
        font-weight: 900;
        font-size: 5vw;
        line-height: 100px;
        letter-spacing: 0.03em;
        background: linear-gradient(90deg, #2028E0 0%, #6EDCFC 69.86%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
        text-fill-color: transparent;
        z-index: 2;

        @media (max-width: 768px) {
            font-size: 8vw;
        }

        @media (max-width: 1215px) {
            line-height: 56px;
        }
    }
    p{
        font-weight: 500;
        font-size: 30px;
        line-height: 45px;
        letter-spacing: 0.04em;
        color: #FFF;
        font-family: 'Gemunu Libre', sans-serif;
        position: relative;
        
        @media (max-width: 576px) {
            font-size: 20px;
            line-height: 25px;
        }
    }
    
`;

export const Img = styled.div`

    width: 45%;
    position: relative;

    img{
        position: absolute;
        width: 100%;
        min-width: 935px;
        top: -80px;
        right: -50px;
        transform: scaleX(-1);
    }

    /* @media (min-width: 1200px) {
        top: 150px;
        right: -400px;
    } */
   

    @media (max-width: 1200px) {
        top: 150px;
        right: -400px;
        }

    @media (max-width: 992px) {
            width: 0%;
            top: -310px;
            right: -678px;
            /* right: -200px; */
    }

    @media (max-width: 768px) {
            top: -320px;
            right: -590px;
    }

    @media (max-width: 576px) {
         top: -290px;
         right: -650px;
    }

    @media (max-width: 360px) {
        top: -300px;
        right: -570px;
    }

   
    


    


    

`;

export const ButtonHeader = styled.a`
    position: absolute;
    font-family: 'Orbitron';
    font-weight: 700;
    font-size: 20px;
    color: #FFFFFF;
    padding: 15px;
    cursor: pointer;
    border: none;
    background: linear-gradient(90deg, #19E5F2 2.23%, #232FE1 99.17%);
    box-shadow: 0px 6px 17px rgba(35, 47, 225, 0.4);
    border-radius: 50px;
    align-self: flex-start;
    margin-top: 40px;
    z-index: 1;
    justify-self: center;
    margin-left: 20px;
    text-decoration: none;

    &:hover{
        color: rgba(255, 255, 255, 1) !important;
        box-shadow: 0 4px 16px rgba(49, 138, 172, 1);
        transition: all 0.2s ease;
    }

`;