import {TokenomicsContainer, Title, Container, Bar, Detail, Details, DetailData} from './TokenomicStyle';
import coin from '../../img/coin.svg';

const Tokenomics = () => {
    return (
        <TokenomicsContainer>
            <Title>
                <h2>TOKENOMICS</h2>
            </Title>
            <Container>
                <div>
                    <img src={coin} />
                </div>
                <Details>
                    <Detail>
                        <DetailData>
                            <Bar></Bar>
                            <p>04%</p>
                        </DetailData>
                        <p className='text'>MARKETING</p>
                    </Detail>
                    <Detail>
                        <DetailData>
                            <Bar></Bar>
                            <p>20%</p>
                        </DetailData>
                        <p className='text'>PREVENTA PINKSALE</p>
                    </Detail>
                    <Detail>
                        <DetailData>
                            <Bar></Bar>
                            <p>04%</p>
                        </DetailData>
                        <p className='text'>ADVIRSOS</p>
                    </Detail>
                    <Detail>
                        <DetailData>
                            <Bar></Bar>
                            <p>01%</p>
                        </DetailData>
                        <p className='text'>AIDROP GRATUITO</p>
                    </Detail>
                    <Detail>
                        <DetailData>
                            <Bar></Bar>
                            <p>08%</p>
                        </DetailData>
                        <p className='text'>INVESTORS</p>
                    </Detail>
                    <Detail>
                        <DetailData>
                            <Bar></Bar>
                            <p>60%</p>
                        </DetailData>
                        <p className='text'>FARMING IN GAME</p>
                    </Detail>
                    <Detail>
                        <DetailData>
                            <Bar></Bar>
                            <p>03%</p>
                        </DetailData>
                        <p className='text'>DEVS</p>
                    </Detail>
                </Details>
            </Container>
        </TokenomicsContainer>
     );
}
 
export default Tokenomics;