import { Fragment } from 'react';
import {Header, Title, Sub, Item, Arrow, Ul, Body} from './MapStyled';
import arrow from '../../img/arrow.svg';

const Map = () => {
    return ( 
        <Fragment>
            <Header>
                <Title>ROADMAP</Title>
                <Sub>CRYPTO CANS</Sub>
            </Header>
            <Body>
                <Item>
                    <p className='q active'>Q1</p>
                    <p className='year active'>2022</p>
                    <Arrow>
                        <img src={arrow} />
                    </Arrow>
                    <Ul>
                        <li>Diseño de la landing page</li>
                        <li>Promoción en redes y Youtube</li>
                        <li>Preventa del Token y nft</li>
                        <li>Lanzamiento de las carreras </li>
                        <li>Contra la maquina (Single Race)</li>
                    </Ul>
                </Item>
                <Item>
                    <p className='q'>Q2</p>
                    <p className='year'>2022</p>
                    <Arrow>
                        <img src={arrow} />
                    </Arrow>
                    <Ul>
                        <li>Lanzamiento del modo de alimento a tus perros</li>
                        <li>Lanzamiento del modo de carreras Rank</li>
                        <li>Lanzamiento del módulo de apuestas</li>
                        <li>Ampliación del equipo de trabajo</li>
                    </Ul>
                </Item>
                <Item>
                    <p className='q'>Q4</p>
                    <p className='year'>2022</p>
                    <Arrow>
                    </Arrow>
                    <Ul>
                        <li>Mini juego de visita a los canódromos para alimentar los perros de otros jugadores</li>
                        <li>Becas</li>
                        <li>Juego en 3d para móviles de competencias en tiempo real contra otros jugadores</li>
                    </Ul>
                </Item>
                
            </Body>
        </Fragment>
     );
}
 
export default Map;