import { useState, Fragment, useLayoutEffect } from 'react';
import styled from 'styled-components';
import './App.css';
import Nav from './components/nav/Nav';
import Loader from './components/loader/Loader';
import Hero from './components/hero/Hero';
import About from './components/about/About';
import Nft from './components/nft/Nft';
import Tokenomics from './components/tokenomics/Tokenomics';
import Map from './components/map/Map';
import Team from './components/team/Team';
import Footer from './components/footer/Footer';
import bg from './img/bg-header.png';

const ContainerApp = styled.header`
  position: relative;
  overflow: hidden;
  /* height: 1120px; */
  
`;

const Header = styled.header`
  background: url(${bg});
  height: 900px;
  background-size: cover;
  /* padding: 0 50px; */
`;

export const EllipseLeft = styled.div`
    position: absolute;
    width: 1063px;
    height: 1020px;
    left: -895px;
    top: 194px;

    background: rgba(25, 229, 242, 0.3);
    filter: blur(472px);
`;

export const EllipseRight = styled.div`
    position: absolute;
    width: 513px;
    height: 513px;
    left: 1002px;
    top: 704px;

    background: rgba(228, 117, 31, 0.26);
    filter: blur(398px);
`;

function App() {

  const [ load, setLoader ] = useState(true);

  useLayoutEffect(() => {
    setTimeout(() => {
      setLoader(false);
    }, 2000);
  }, [load]);	

  return (
    <Fragment>
    {load && <Loader />}
    {!load &&
    <ContainerApp>
      <EllipseLeft />
      <EllipseRight />
      <Header>
          <Nav />
          <Hero />
      </Header>
      <About />
      <Nft />
      <Tokenomics />
      <Map />
      <Team />
      <Footer />
    </ContainerApp>}
    </Fragment>
  );
}

export default App;
