import styled from 'styled-components';

export const Container = styled.div`
    display: flex;
    flex-direction: column;
    padding: 150px 70px;
    gap: 100px;
`;

export const Header = styled.div`

    h2{
        font-family: 'Orbitron';
        font-weight: 900;
        font-size: 7vw;
        line-height: 100px;
        letter-spacing: 0.03em;
        text-transform: uppercase;
        background: linear-gradient(92.21deg, #F77A12 0.86%, #FF3737 100.29%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
        text-fill-color: transparent;
        margin-bottom: 50px;

        @media (max-width: 600px) {
            line-height: 50px;
        }

    }

`;

export const Bar = styled.div`
    width: 100%;
    height: 3px;
    background: linear-gradient(90deg, #F3BA2F -0.65%, #FF3737 100.03%);


`;

export const CardContainer = styled.div`
    display: flex;
    justify-content: space-evenly;
    align-items: flex-end;
    flex-wrap: wrap;

    @media (max-width: 1200px) {
        gap: 20px 0;
    }

    .main{
        width: 410px;
        height: 530px;

        @media (max-width: 576px) {
            width: 280px;
            height: 370px;
        }

        h3{
            font-size: 30px;

                @media (max-width: 576px) {
                    font-size: 20px;
                }
        }

    }
`;

export const Card = styled.div`
    display: flex;
    flex-direction: column;
    padding: 40px;;
    background-image: -webkit-linear-gradient(left, #F3B52F 0%, #FF3737 100%), -webkit-linear-gradient(left, #F3B52F 0%, #FF3737 100%);
    background-image: -moz-linear-gradient(left, #F3B52F 0%, #FF3737 100%), -moz-linear-gradient(left, #F3B52F 0%, #FF3737 100%);
    background-image: -o-linear-gradient(left, #F3B52F 0%, #FF3737 100%), -o-linear-gradient(left, #F3B52F 0%, #FF3737 100%);
    background-image: linear-gradient(to right, #F3B52F 0%, #FF3737 100%), linear-gradient(to right, #F3B52F 0%, #FF3737 100%);
    border-left: 3px solid #F3B52F;
    border-right: 3px solid #FF3737;
    border-top: 3px solid #F3B52F;
    border-bottom: 3px solid #FF3737;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    background-position: 0 0, 0 100%;
    background-repeat: no-repeat;
    -webkit-background-size: 100% 1px;
    -moz-background-size: 100% 1px;
    background-size: 100% 1px;
    border-radius: 20px;
    width: 280px;
    height: 370px;

    h3{
        font-family: 'Orbitron';
        font-weight: 700;
        font-size: 20px;
        line-height: 38px;
        color: rgba(255, 255, 255, 0.9);
    }

    p{
        font-family: 'Gemunu Libre';
        font-weight: 600;
        font-size: 18px;
        line-height: 29px;
        letter-spacing: 1px;
        color: rgba(255, 255, 255, 0.9);
    }
    
    &:hover{
        box-shadow: 8px 8px 32px rgba(236, 240, 241, 0.2);
        transition: all 0.2s ease;
    }
`;