import { NavStyled, NavLink, Link, NavSocial, LinkSocial, Wallet, Bar, EllipseSocial } from './NavStyled';
import logo from '../../img/logo.svg';
import discord from '../../img/discord.svg';
import telegram from '../../img/telegram.svg';
import twitter from '../../img/twitter.svg';
import youtube from '../../img/youtube.svg';
import metamask from '../../img/metamask.svg';

const Nav = () => {

    return ( 
        <NavStyled>
            <NavLink>
                <img src={logo} />
                <Link>
                    <li><a href='/dapp'>Dashboard</a></li>
                    <li><a href='/market'>Marketplace</a></li>
                    <li><a href='race'>Race</a></li>
                </Link>
            </NavLink>
            <NavSocial>
                <LinkSocial>
                    <li><a href='#'><img src={discord} /></a></li>
                    <li><a href='#'><img src={telegram} /></a></li>
                    <li><a href='#'><img src={twitter} /></a></li>
                    <li><a href='#'><img src={youtube} /></a></li>
                    <Bar />
                </LinkSocial>  
                <EllipseSocial />         
                <Wallet>
                    <a href='/dapp'><span>Wallet</span></a>
                    <img src={metamask} />
                </Wallet>
            </NavSocial>
        </NavStyled> 
    );
}
 
export default Nav;